      <li :class="{'green':bankColor[item.code]==='green','red':bankColor[item.code]==='red','yellow':bankColor[item.code]==='yellow'}" v-for="(item, i) in bankCardList" :key="i">
        <div class="list-content">
          <div class="top-text">
            <div class="logo">
              <img :src="item.logo" alt="">
            </div>
            <div class="text">
              <h2>{{item.bankName}}</h2>
              <span>储蓄卡</span>
            </div>
          </div>
          <div class="footer-text">
            <p>****&nbsp;&nbsp;****&nbsp;&nbsp;****&nbsp;&nbsp;&nbsp;&nbsp;{{item.no.slice(-4)}}</p>
