</template>

<script>

import { queryBankCardList } from '@/api/index.js';
import { banks } from '@/assets/js/fields.js';

export default {
  name: 'bankcardList',
  data() {
    return {
      bankCardList: {},
      bankColor: {
        ABC: 'green',
        CCB: 'blue',
        ICBC: 'red',
        BOC: 'red',
        BOCOM: 'blue',
        CNCB: 'red',
        CEB: 'yellow',
        GDB: 'red',
        PAB: 'yellow',
        CIB: 'blue',
        SPDB: 'blue',
        BOS: 'blue',
        BCCB: 'red',
        CMB: 'red',
        PSBC: 'green',
        HXB: 'red',
        CMBC: 'blue',
        BOJS: 'blue',
      },
    };
  },
  mounted() {
    this.initData();
  },
  computed: {},
  methods: {
    initData() {
      queryBankCardList()
        .then((result) => {
          if (result) {
            this.bankCardList = result;
            this.bankCardList.forEach((element) => {
              element.bankName = banks[element.code] || element.code;
            });
          }
        })
        .catch(err => console.error(err));
    },
  },
  components: {},
};
</script>
